<template>
  <div class="search">
    <div class="searchtop">
      <div class="retbtn" @click="retClick">
        <span class="iconfont icon-zuo"></span>
      </div>
      <div class="searchbox">
        <div class="inputbox">
          <div class="iconinp">
            <span class="iconfont icon-sousuoxiao"></span>
            <input
              type="text"
              placeholder="请输入商品名称"
              v-model="searchText"
            />
            <div class="chacha" v-show="searchText" @click="clearInp">
              <span class="iconfont icon-chacha"></span>
            </div>
          </div>
        </div>
        <div class="btnbox" @click="searchClick">搜索</div>
      </div>
    </div>
    <search-history
      :history="history"
      @historyClick="historyClick"
      @removeHistory="removeHistory"
    ></search-history>
  </div>
</template>

<script>
import mixin from "@/mixin/index";
import searchHistory from "@/components/SearchHistory.vue";
export default {
  mixins: [mixin],
  components: {
    searchHistory,
  },
  data() {
    return {
      searchText: "",
      history: [],
    };
  },
  watch: {
    isclick(val) {
      console.log(val);
      this.updateSearchHistory();
    },
  },
  created() {
    this.updateSearchHistory();
  },
  methods: {
    updateSearchHistory() {
      let list = window.localStorage.getItem("searchhistory");
      if (list) {
        this.history = JSON.parse(list);
      }
    },
    retClick() {
      this.$router.go(-1);
    },
    historyClick(item) {
      this.searchText = item;
      this.searchClick();
    },
    removeHistory() {
      this.history = [];
      window.localStorage.removeItem("searchhistory");
    },
    searchClick() {
      if (this.searchText) {
        this.$router.push("/searchlist/" + this.searchText);
        let flag = true;
        this.history.forEach((item) => {
          if (item == this.searchText) {
            flag = false;
          }
        });
        if (flag) {
          window.localStorage.setItem(
            "searchhistory",
            JSON.stringify([...this.history, this.searchText])
          );
        }
      } else {
        this.$toast("请输入内容后搜索");
      }
    },
    clearInp() {
      this.searchText = "";
    },
    gotoHome() {
      this.$router.push("/home");
    },
  },
};
</script>

<style lang="scss" scoped>
.search {
  position: relative;
  padding-top: 55px;
  .searchtop {
    position: fixed;
    width: 100%;
    top: 0;
    .retbtn {
      position: fixed;
      top: 15px;
      left: 15px;
      width: 30px;
      height: 30px;
      background: rgba($color: #999, $alpha: 0.8);
      text-align: center;
      color: #fff;
      line-height: 30px;
      font-size: 14px;
      border-radius: 15px;
      .icon-zuo {
        padding-left: 7px;
        font-size: 18px;
      }
    }
  }
}

.searchbox {
  width: 100%;
  height: 55px;
  box-sizing: border-box;
  background-color: #fff;
  padding: 10px 15px;
  display: flex;
  border-bottom: 0.5px solid #e8e8e8;

  .inputbox {
    flex: 1;
    height: 34px;
    background-color: #f7f7f7;
    border-radius: 18px;
    font-size: 13px;
    line-height: 34px;
    .iconinp {
      width: 100%;
      height: 100%;
      padding-right: 15px;
      box-sizing: border-box;
      display: flex;
      .icon-sousuoxiao {
        font-size: 14px;
        width: 35px;
        display: block;
        text-align: center;
      }
      input {
        flex: 1;
        border: none;
        background: none;
      }
      .chacha {
        .icon-chacha {
          color: #c0c1c2;
          font-size: 15px;
        }
      }
    }
  }
  .btnbox {
    width: 60px;
    height: 34px;
    background-color: #e84040;
    margin-left: 10px;
    box-shadow: 0px 2px 3px 0px rgb(234 65 65 / 25%);
    border-radius: 17px;
    font-size: 15px;
    color: #fff;
    text-align: center;
    line-height: 34px;
  }
  .cancel {
    width: 32px;
    font-size: 16px;
    line-height: 34px;
    text-align: center;
    margin-left: 10px;
    color: #5f646e;
  }
}
</style>