<template>
  <div class="record">
    <div class="recordtop">
      <div class="title">历史记录</div>
      <div>
        <span class="iconfont icon-lajitong" @click="delHistory"></span>
      </div>
    </div>
    <div class="recordcont">
      <span
        v-for="(item, index) in history"
        :key="item + index"
        @click="gotoSearch(item)"
        >{{ item }}</span
      >
    </div>
  </div>
</template>

<script>
export default {
  props: ["history"],
  methods: {
    gotoSearch(item) {
      this.$emit("historyClick", item);
    },
    delHistory() {
      this.$dialog
        .confirm({
          title: "是否删除历史记录?",
        })
        .then(() => {
          this.$emit("removeHistory");
        })
        .catch(() => {
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.record {
  width: 100%;
  min-height: 90px;
  padding: 20px 15px 5px 15px;
  box-sizing: border-box;
  background: #fff;
  .recordtop {
    width: 100%;
    height: 13px;
    display: flex;
    line-height: 13px;
    .title {
      flex: 1;
      font-size: 13px;
      color: #313336;
      font-weight: 700;
    }
  }
  .recordcont {
    width: 100%;
    padding-top: 12px;
    min-height: 40px;
    span {
      display: inline-block;
      height: 30px;
      background: #f5f5f5;
      border-radius: 15px;
      padding: 0 13px;
      margin-right: 8px;
      margin-bottom: 10px;
      text-align: center;
      line-height: 30px;
      overflow: hidden;
    }
  }
}
</style>